import { Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { DatePipe } from '@angular/common';
import { ActivatedRoute, Router } from '@angular/router';
import { ApiService } from '../services/api.service';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.css']
})
export class EditComponent implements OnInit {
  id: number;
  private sub: any;
  user: any;
  roles: any = [];
  myForm: any;

  constructor(
    private route: ActivatedRoute,
    private api: ApiService,
    private formBuilder: FormBuilder,
    private datePipe: DatePipe,
    private router: Router) { }

  ngOnInit() {
    this.sub = this.route.params.subscribe(params => {
      this.id = +params['id'];
    });
    this.getUser(this.id);
    this.getRoles();
    this.myForm = this.formBuilder.group({
      id: [this.id],
      name: [''],
      surname: [''],
      birthdate: [''],
      login: [''],
      password: [''],
      roleid: ['']
    });
  }

  getUser(id: number) {
    this.api.getUserById(id).subscribe((data) => {
      this.user = data[0];
    });
  }

  getRoles() {
    this.api.getRoles().subscribe((data) => {
      this.roles = data;
    });
  }

  submitHandler() {
    this.myForm.value.birthdate = this.datePipe.transform(this.myForm.value.birthdate, 'yyyy-MM-dd');
    this.api.updateUser(this.myForm.value, this.myForm.value.id).subscribe(() => {
      this.getUser(this.id);
    });
  }

}
